body {
   background-color: #a4f;
}

.App {
   background-color: #204;
   min-height: 500px;
   width: 400px;
   margin: 40px auto;
   border-radius: 20px;
}

#to-do-form input {
   background-color: #406;
   border: 0;
   width: 200px;
   height: 50px;
   padding: 0 20px;
   margin: 20px;
   font-size: 19px;
   border-radius: 10px;
   color: #fff;
}

#to-do-form input::placeholder {
   color: #fff8;

}

#to-do-form input:focus,
#to-do-form button:focus {
   outline: none;
}

#to-do-form button {
   height: 50px;
   width: 80px;
   border-radius: 5px;
   border: 0;
   background-color: #2af;
   cursor: pointer;
   font-weight: bolder;
   color: #204;

}